@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap");

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul,
ol li,
ul li {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
  color: #000;
}
strong {
  font-weight: bold;
}
button {
  background-color: transparent;
  cursor: pointer;
  border: none;
}
legend,
caption {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  line-height: 0;
  visibility: hidden;
  overflow: hidden;
  text-indent: -9999px;
}
.hidden {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  line-height: 0;
  visibility: hidden;
  overflow: hidden;
  text-indent: -9999px;
}
.offscreen {
  clip: rect(1px 1px 1px 1px);
  display: inline-block;
  position: absolute;
  width: 1px;
  height: 1px;
  line-height: 0;
  font-size: 1px;
  overflow: hidden;
}
input[type="text"],
input[type="search"],
input[type="password"] {
  border: none;
}
body {
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 400;
  font-size: 14px;
}
a, button {
  font-family: "Noto Sans KR", sans-serif;
  font-size: 14px;
}
img{width:100%; height:100%;}
